.settings-page {
  padding: 20px;
  max-width: 100%;
}

.page-title {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
  font-weight: 600;
}

/* Стили пользовательской информации */
.user-info {
  display: flex;
  align-items: center;
  margin-bottom: 1.75rem;
  padding: 1rem;
  background-color: var(--bg-secondary);
  border-radius: 12px;
  box-shadow: var(--card-shadow);
  border: 1px solid var(--border-color);
}

.user-avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
  font-weight: 700;
  margin-right: 1rem;
}

.user-details {
  flex: 1;
}

.user-name {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.2rem;
  color: var(--text-color);
}

.user-username {
  font-size: 0.95rem;
  color: var(--text-secondary);
}

/* Стили секций настроек */
.settings-section {
  background-color: var(--bg-secondary);
  border-radius: 12px;
  padding: 1.25rem;
  margin-bottom: 1.5rem;
  box-shadow: var(--card-shadow);
  border: 1px solid var(--border-color);
}

.section-title {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1.25rem;
  color: var(--text-color);
  padding-bottom: 0.5rem;
  border-bottom: 1px solid var(--border-color);
}

/* Стили элементов настроек */
.setting-item {
  display: flex;
  align-items: center;
  padding: 1rem 0;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.setting-item:not(:last-child) {
  border-bottom: 1px solid var(--border-color);
}

.setting-item:hover {
  background-color: var(--bg-hover);
}

.setting-icon {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: rgba(var(--primary-color-rgb), 0.15);
  color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.25rem;
  margin-right: 1rem;
}

.setting-icon.danger {
  background-color: rgba(244, 67, 54, 0.15);
  color: var(--danger-color);
}

.setting-content {
  flex: 1;
}

.setting-title {
  font-weight: 500;
  margin-bottom: 0.25rem;
  color: var(--text-color);
}

.setting-title.danger {
  color: var(--danger-color);
}

.setting-description {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.setting-arrow {
  margin-left: 1rem;
  font-size: 1.5rem;
  color: var(--text-secondary);
}

/* Стили переключателя */
.setting-toggle {
  margin-left: 1rem;
}

.toggle-switch {
  width: 50px;
  height: 28px;
  background-color: var(--border-color);
  border-radius: 34px;
  position: relative;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.toggle-switch.active {
  background-color: var(--primary-color);
}

.toggle-knob {
  position: absolute;
  height: 22px;
  width: 22px;
  left: 3px;
  top: 3px;
  background-color: white;
  border-radius: 50%;
  transition: transform 0.3s ease;
}

.toggle-switch.active .toggle-knob {
  transform: translateX(22px);
}

/* Стили информации о приложении */
.app-info {
  text-align: center;
  margin-top: 2rem;
  padding-top: 1rem;
  border-top: 1px solid var(--border-color);
  color: var(--text-secondary);
}

.app-version {
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}

.app-copyright {
  font-size: 0.85rem;
}

/* Адаптивные стили */
@media (min-width: 768px) {
  .settings-page {
    max-width: 700px;
    margin: 0 auto;
  }
}

@media (max-width: 480px) {
  .user-avatar {
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }
  
  .user-name {
    font-size: 1.1rem;
  }
  
  .setting-icon {
    width: 40px;
    height: 40px;
    font-size: 1.1rem;
  }
} 