.home-page {
  padding: 20px;
  position: relative;
  max-width: 100%;
}

.page-title {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
  font-weight: 600;
}

/* Фильтры */
.habit-filters {
  display: flex;
  justify-content: center;
  margin-bottom: 1.75rem;
  gap: 10px;
}

.filter-button {
  padding: 8px 18px;
  background: none;
  border: 1px solid var(--border-color);
  border-radius: 25px;
  color: var(--text-color);
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
}

.filter-button:hover {
  border-color: var(--primary-color);
  transform: translateY(-2px);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
}

.dark-mode .filter-button:hover {
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
}

.filter-button.active {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  color: white;
  box-shadow: 0 3px 10px rgba(var(--primary-color-rgb), 0.3);
}

/* Список привычек */
.habits-list {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.habit-item {
  background-color: var(--bg-secondary);
  border-radius: 12px;
  border: 1px solid var(--border-color);
  overflow: hidden;
  box-shadow: var(--card-shadow);
  transition: transform var(--transition-medium), box-shadow var(--transition-medium), border-color var(--transition-fast);
  cursor: pointer;
  position: relative;
}

.habit-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.25);
  border-color: var(--primary-color);
}

.dark-mode .habit-item:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.habit-item.completed {
  border-left: 4px solid var(--success-color);
}

.habit-item::after {
  content: '';
  position: absolute;
  top: -100%;
  left: -100%;
  width: 50%;
  height: 50%;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0) 20%,
    rgba(255, 255, 255, 0.1) 40%,
    rgba(255, 255, 255, 0) 60%
  );
  transform: rotate(45deg);
  transition: all 0.6s ease;
}

.habit-item:hover::after {
  top: 100%;
  left: 100%;
}

.habit-item-content {
  padding: 1.25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.habit-details {
  flex: 1;
}

.habit-title {
  font-weight: 600;
  font-size: 1.15rem;
  margin-bottom: 0.5rem;
  color: var(--text-color);
  transition: color var(--transition-fast);
}

.habit-item:hover .habit-title {
  color: var(--primary-color);
}

.habit-meta {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.habit-category {
  display: inline-block;
  padding: 0.3rem 0.7rem;
  border-radius: 12px;
  font-size: 0.8rem;
  background-color: rgba(var(--primary-color-rgb), 0.1);
  color: var(--text-color);
  transition: transform var(--transition-fast), background-color var(--transition-fast);
}

.habit-item:hover .habit-category {
  background-color: rgba(var(--primary-color-rgb), 0.2);
  transform: translateY(-2px);
}

.habit-streak {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  color: var(--warning-color);
  font-weight: 500;
}

.streak-icon {
  margin-right: 0.25rem;
  font-size: 1rem;
  animation: flicker 1.5s infinite alternate;
}

@keyframes flicker {
  0%, 18%, 22%, 25%, 53%, 57%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  20%, 24%, 55% {
    opacity: 0.8;
    transform: scale(0.9);
  }
}

.status-badge {
  display: inline-block;
  padding: 0.3rem 0.7rem;
  border-radius: 12px;
  font-size: 0.8rem;
  text-align: center;
  font-weight: 500;
  transition: transform var(--transition-fast);
}

.status-badge.completed {
  background-color: var(--success-color);
  color: white;
  animation: pulse 2s infinite;
}

.status-badge.pending {
  background-color: var(--warning-color);
  color: white;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

.habit-actions {
  display: flex;
  gap: 0.5rem;
  padding: 0.75rem;
  background-color: rgba(var(--primary-color-rgb), 0.05);
  border-top: 1px solid var(--border-color);
  transition: background-color var(--transition-fast);
}

.habit-item:hover .habit-actions {
  background-color: rgba(var(--primary-color-rgb), 0.1);
}

.action-button {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  transition: transform var(--transition-fast), background-color var(--transition-fast), color var(--transition-fast);
}

.action-button:hover {
  transform: scale(1.1);
  color: var(--text-color);
  background-color: var(--bg-hover);
}

.action-button.complete {
  color: var(--success-color);
}

.action-button.complete:hover {
  background-color: rgba(76, 175, 80, 0.1);
}

.action-button.edit {
  color: var(--info-color);
}

.action-button.edit:hover {
  background-color: rgba(33, 150, 243, 0.1);
}

.action-button.delete {
  color: var(--danger-color);
}

.action-button.delete:hover {
  background-color: rgba(244, 67, 54, 0.1);
}

.action-button.reset {
  color: var(--warning-color);
}

.action-button.reset:hover {
  background-color: rgba(255, 152, 0, 0.1);
}

.filters {
  display: flex;
  gap: 0.5rem;
  margin-top: 1rem;
  overflow-x: auto;
  padding-bottom: 0.5rem;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.filters::-webkit-scrollbar {
  display: none;
}

.filter-button {
  padding: 0.6rem 1.2rem;
  border-radius: 12px;
  background-color: var(--bg-secondary);
  border: 1px solid var(--border-color);
  color: var(--text-secondary);
  font-weight: 500;
  cursor: pointer;
  white-space: nowrap;
  transition: transform var(--transition-fast), background-color var(--transition-fast), box-shadow var(--transition-fast), color var(--transition-fast);
}

.filter-button:hover {
  transform: translateY(-2px);
  color: var(--text-color);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.filter-button.active {
  background-color: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
  box-shadow: 0 4px 8px rgba(var(--primary-color-rgb), 0.3);
}

/* Кнопка добавления привычки */
.floating-add-button {
  position: fixed;
  bottom: 80px;
  right: 20px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: var(--primary-color);
  color: white;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  box-shadow: 0 4px 15px rgba(var(--primary-color-rgb), 0.4);
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease, box-shadow 0.3s ease;
  z-index: 10;
}

.floating-add-button:hover {
  transform: translateY(-5px) scale(1.05);
  background-color: var(--primary-color-dark);
  box-shadow: 0 6px 20px rgba(var(--primary-color-rgb), 0.5);
}

.floating-add-button:active {
  transform: translateY(0) scale(0.95);
  box-shadow: 0 2px 10px rgba(var(--primary-color-rgb), 0.3);
}

/* Пустое состояние */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 4rem 1rem;
  background-color: var(--bg-secondary);
  border-radius: 12px;
  border: 1px solid var(--border-color);
  margin-top: 2rem;
  animation: fadeIn var(--transition-medium);
}

.empty-state p {
  color: var(--text-secondary);
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.empty-state .btn {
  margin-top: 1rem;
}

.empty-list {
  text-align: center;
  padding: 2rem;
  color: var(--text-secondary);
  background-color: var(--bg-secondary);
  border-radius: 12px;
  border: 1px solid var(--border-color);
}

/* Состояния загрузки и ошибки */
.loading-container, 
.error-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  text-align: center;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(var(--primary-color-rgb), 0.1);
  border-top-color: var(--primary-color);
  border-radius: 50%;
  animation: spin 1s ease-in-out infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.error-message {
  color: var(--danger-color);
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  font-weight: 500;
}

/* Адаптивные стили */
@media (min-width: 768px) {
  .home-page {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .habit-item {
    flex-direction: row;
  }
  
  .filter-button {
    padding: 0.75rem 1.5rem;
  }
  
  .status-badge {
    font-size: 0.9rem;
    padding: 0.4rem 0.8rem;
  }
}

@media (max-width: 480px) {
  .home-page {
    padding: 15px;
  }
  
  .habit-title {
    font-size: 1.05rem;
  }
  
  .habit-item-content {
    padding: 1rem;
  }
  
  .habit-actions {
    padding: 0.5rem;
  }
  
  .action-button {
    width: 36px;
    height: 36px;
  }
  
  .mobile-add-button {
    width: 55px;
    height: 55px;
    font-size: 1.3rem;
  }
}

/* Анимации для страницы */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
} 