/* Сброс стилей */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Шрифты */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

/* Основные стили приложения */
:root {
  --bg-primary: var(--tg-theme-bg-color, #1a1a1a);
  --bg-secondary: var(--tg-theme-secondary-bg-color, #2c2c2c);
  --bg-hover: var(--tg-theme-secondary-bg-color, #3d3d3d);
  --text-color: var(--tg-theme-text-color, #e0e0e0);
  --text-secondary: var(--tg-theme-hint-color, #a0a0a0);
  --border-color: var(--tg-theme-hint-color, #444444);
  --primary-color: var(--tg-theme-button-color, #8c66ff);
  --primary-color-rgb: 140, 102, 255;
  --primary-color-dark: #7452e3;
  --success-color: #4caf50;
  --warning-color: #ff9800;
  --danger-color: #f44336;
  --info-color: #2196f3;
  --card-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  
  /* Анимации и переходы */
  --transition-fast: 0.2s ease;
  --transition-medium: 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
  --transition-slow: 0.5s ease;
  --scale-hover: scale(1.02);
  --scale-active: scale(0.98);
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  background-color: var(--bg-primary);
  color: var(--text-color);
  min-height: 100vh;
  padding-bottom: 70px;
  overflow-x: hidden;
}

.app {
  width: 100%;
  max-width: 100%;
  min-height: 100vh;
  position: relative;
}

/* Стили для прогресс-бара */
.progress-circle {
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto;
}

.progress-circle svg {
  width: 100%;
  height: 100%;
  transform: rotate(-90deg);
}

.progress-circle-bg {
  fill: none;
  stroke: rgba(var(--primary-color-rgb), 0.2);
  stroke-width: 8;
}

.progress-circle-value {
  fill: none;
  stroke: var(--primary-color);
  stroke-width: 8;
  stroke-dasharray: 251;
  stroke-linecap: round;
  transition: stroke-dashoffset var(--transition-slow);
}

.progress-circle-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-color);
}

/* Базовые стили для кнопок */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  background-color: var(--primary-color);
  color: white;
  font-weight: 600;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  text-decoration: none;
  transition: transform var(--transition-medium), background-color var(--transition-fast), box-shadow var(--transition-fast);
  box-shadow: 0 4px 8px rgba(var(--primary-color-rgb), 0.3);
}

.btn:hover, .btn:focus {
  transform: var(--scale-hover);
  box-shadow: 0 6px 12px rgba(var(--primary-color-rgb), 0.4);
}

.btn:active {
  transform: var(--scale-active);
  background-color: var(--primary-color-dark);
}

.btn-icon {
  margin-right: 0.5rem;
  font-size: 1.2rem;
}

.btn-secondary {
  background-color: transparent;
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  box-shadow: none;
}

.btn-secondary:hover, .btn-secondary:focus {
  background-color: rgba(var(--primary-color-rgb), 0.1);
  box-shadow: 0 4px 8px rgba(var(--primary-color-rgb), 0.2);
}

.btn-danger {
  background-color: var(--danger-color);
  box-shadow: 0 4px 8px rgba(244, 67, 54, 0.3);
}

.btn-danger:hover, .btn-danger:focus {
  box-shadow: 0 6px 12px rgba(244, 67, 54, 0.4);
}

.btn-danger:active {
  background-color: #d32f2f;
}

/* Общие стили для карточек */
.card {
  background-color: var(--bg-secondary);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  box-shadow: var(--card-shadow);
  border: 1px solid var(--border-color);
  transition: transform var(--transition-medium), box-shadow var(--transition-medium);
}

.card:hover {
  transform: var(--scale-hover);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.25);
}

/* Стили для полей ввода */
.input-field {
  width: 100%;
  padding: 0.75rem 1rem;
  background-color: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  color: var(--text-color);
  font-size: 1rem;
  font-family: inherit;
  transition: border-color var(--transition-fast), box-shadow var(--transition-fast);
}

.input-field:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(var(--primary-color-rgb), 0.25);
  outline: none;
}

textarea.input-field {
  min-height: 100px;
  resize: vertical;
}

/* Стили для заголовков страниц */
.page-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid var(--border-color);
  transition: color var(--transition-fast);
}

.page-title:hover {
  color: var(--primary-color);
}

/* Стили для анимированного спиннера загрузки */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-spinner {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 3px solid rgba(var(--primary-color-rgb), 0.3);
  border-radius: 50%;
  border-top-color: var(--primary-color);
  animation: spin 1s ease-in-out infinite;
}

/* Анимации страниц */
.page-enter {
  opacity: 0;
  transform: translateY(20px);
}

.page-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity var(--transition-medium), transform var(--transition-medium);
}

.page-exit {
  opacity: 1;
  transform: translateY(0);
}

.page-exit-active {
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity var(--transition-medium), transform var(--transition-medium);
}

/* Адаптивные стили */
@media (min-width: 768px) {
  .app {
    padding: 0 2rem;
  }
  
  .card {
    padding: 2rem;
  }
  
  .btn {
    padding: 0.75rem 2rem;
  }
  
  .page-title {
    font-size: 1.75rem;
  }
}

@media (min-width: 992px) {
  .app {
    max-width: 900px;
    margin: 0 auto;
  }
}

@media (max-width: 480px) {
  body {
    padding-bottom: 65px;
  }
  
  .card {
    padding: 1.25rem;
  }
  
  .btn {
    padding: 0.625rem 1.25rem;
    font-size: 0.95rem;
  }
  
  .page-title {
    font-size: 1.35rem;
    margin-bottom: 1.25rem;
    padding-bottom: 0.625rem;
  }
  
  .input-field {
    padding: 0.625rem 0.875rem;
    font-size: 0.95rem;
  }
}

/* Утилиты */
.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.justify-between {
  justify-content: space-between;
}

.items-center {
  align-items: center;
}

.gap-2 {
  gap: 0.5rem;
}

.gap-4 {
  gap: 1rem;
}

.mt-4 {
  margin-top: 1rem;
}

.mb-4 {
  margin-bottom: 1rem;
}

.w-full {
  width: 100%;
} 